.center-div{
    /* display: flex; */
    padding-top: 50px;
    width: 50%;
    margin: auto;

}

.center-div .notes{
    width: 300px;
    color: white;
    background-color: rgb(145, 112, 173);
    margin-right: 20px;
    padding: 5px;
    border-radius: 5px;
    word-break: break-all;
}

.center-div button{
    background-color: brown;
    border-radius: 5px;
    
}

@media screen and (max-width:1269px){
    .center-div{
        width: 100%;
    }
    /* .main-div{
        width: 90%;
    } */
    .center-div{
        width: 90%;
        margin: auto;
    }
    .center-div .notes{
        color: white;
        background-color: rgb(145, 112, 173);
        padding: 5px;
        border-radius: 5px;
        word-break: break-all;

    }
}
